import React from 'react'
import { url } from 'src/helpers/helpers';
import {userContext} from '../../context/UserContext'
import Select from 'react-select'
import { toast,ToastContainer } from 'react-toastify';

export default function Create() {
    const {user,setLoad} = React.useContext(userContext);
    const [allRoles,setAllRoles] = React.useState([]);

    const [currentRole,setCurrentRole] = React.useState('');
    const [name,setName] = React.useState('');
    const [email,setEmail] = React.useState('');

    const [password,setPassword] = React.useState('');
    
    React.useEffect(()=>{
        setLoad(true)
        async function fetchData(){
            const response = await fetch(url + 'rolelist',{
                method : 'GET',
                headers : {
                    'Authorization' : user.token
                }
            })

            if (response.ok === true){
                const data = await response.json();
                setLoad(false)
                if (data.status === 200){
                    setAllRoles(data?.roles_list);
                }else{
                    toast.error(data.message);
                }
            }else{
                toast.error('Oops something went wrong!')
            }
        }
        fetchData().catch(err=>toast.error('Internal server error!'));
    },[])

    const handleSubmit = e => {
        setLoad(true)
        e.preventDefault();
        const formData = new FormData();
        formData.append('name',name);
        formData.append('email',email);
        formData.append('password',password);
        formData.append('role_id',currentRole)

        async function submitData(){
            const response = await fetch(url + 'createuser',{
                method : 'POST',
                headers : {
                    'Authorization' : user?.token
                },
                body : formData
            })

            if (response.ok === true){
                const data = await response.json();
                setLoad(false)
                if (data.status === 200){
                    return window.location = window.location.origin + '/#/userList'
                }else{
                    toast.error(data.message)
                }
            }
        }

        submitData();
    }

    return (
        <div className="container create-page-main-section">
            <ToastContainer />
            <form onSubmit={e=>handleSubmit(e)}>
                <div className='p-sm-5 create-form-field'>
                    <div class="form-group row">
                        <label class="col-sm-2 col-form-label" for="exampleFormControlSelect1">Username:<span className='required-label'>*</span></label>
                        <div class="d-flex align-items-sm-center col-sm-10">
                            <input type="text" value={name} onChange={e=>setName(e.target.value)} className="form-control" />    
                        </div>
                    </div>
                    <div class="form-group row">
                        <label class="col-sm-2 col-form-label" for="exampleFormControlSelect1">Email:<span className='required-label'>*</span></label>
                        <div class="d-flex align-items-sm-center col-sm-10">
                            <input type="email" value={email} onChange={e=>setEmail(e.target.value)} className="form-control" />    
                        </div>
                    </div>
                    <div class="form-group row">
                        <label class="col-sm-2 col-form-label" for="exampleFormControlSelect1">Role:<span className='required-label'>*</span></label>
                        <div class="d-flex align-items-sm-center col-sm-10">
                            <select className='form-control' value={currentRole} onChange={e=>setCurrentRole(e.target.value)}>
                                <option value="">Select Roles</option>
                                {allRoles.map((item,index)=>(
                                    <option key={index} value={item.id}>{item.name}</option>
                                ))}
                            </select>
                        </div>
                    </div>
                    <div class="form-group row">
                        <label for="inputPassword" class="col-sm-2 col-form-label">Password:<span className='required-label'>*</span></label>
                        <div class="d-flex align-items-sm-center col-sm-10">
                            <input value={password} onChange={e=>setPassword(e.target.value)} type="password" class="form-control" id="inputPassword" />
                        </div>
                    </div>
                    
                </div>
                <div class="d-flex justify-content-center create-catagory-btns">
                    <button type="submit" class="font-weight-bold m-3 py-2 px-4 btn btn-danger">Cancel<i
                        class="px-2 fa fa-times" aria-hidden="true"></i></button>
                    <button type="submit" class="font-weight-bold m-3 py-2 px-4 btn btn-success">Save<i
                        class="px-2 fa fa-floppy-o" aria-hidden="true"></i></button>
                </div>
            </form>
        </div>
    )
}
